/* Container styles */
.container {
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    height: 100vh;
    background-color: #f4f4f4; /* Background color */
  }
  
  /* Title styles */
  h1 {
    font-size: 36px;
    color: #333; /* Text color */
    margin-bottom: 20px;
    text-align: center;
    text-shadow: 2px 2px 4px rgba(0, 0, 0, 0.2); /* Text shadow */
  }
  
  /* Input styles */
  input[type="text"] {
    padding: 10px;
    font-size: 18px;
    border: 2px solid #ccc;
    border-radius: 5px;
    margin-bottom: 10px;
    width: 300px;
    text-align: center;
    outline: none;
  }
  
  /* Button styles */
  button {
    padding: 10px 20px;
    font-size: 18px;
    background-color: #007bff; /* Button color */
    color: #fff;
    border: none;
    border-radius: 5px;
    cursor: pointer;
    transition: background-color 0.3s ease;
  }
  
  button:hover {
    background-color: #0056b3; /* Hover color */
  }
  
  /* Error message styles */
  .error {
    color: #f00; /* Error message color */
    font-size: 16px;
    text-align: center;
    margin-top: 10px;
  }
  
  /* Results styles */
  .results {
    text-align: center;
  }
  
  ul {
    list-style: none;
    padding: 0;
  }
  
  li {
    margin: 20px 0;
  }
  
  a {
    color: #007bff; /* Link color */
    text-decoration: none;
    font-weight: bold;
  }
  
  a:hover {
    text-decoration: underline;
  }
  /* Button hover effect */
button:hover {
    background-color: #0056b3;
    transform: scale(1.05); /* Scale up on hover */
  }
/* Fade-in animation for results */
ul {
    list-style: none;
    padding: 0;
    opacity: 0; /* Start with zero opacity */
    animation: fadeIn 0.5s ease forwards;
  }
  
  @keyframes fadeIn {
    to {
      opacity: 1; /* Fade in to full opacity */
    }
  }
/* Box shadow on hover for results */
li:hover {
    box-shadow: 0 0 10px rgba(0, 0, 0, 0.2);
  }
/* Highlight on link hover */
a:hover {
    color: #ff6600; /* Hover color */
  }
/* Pulse animation for error message */
.error {
    color: #f00;
    font-size: 16px;
    text-align: center;
    margin-top: 10px;
    animation: pulse 1.5s infinite;
  }
  
  @keyframes pulse {
    0% {
      transform: scale(1);
    }
    50% {
      transform: scale(1.05);
    }
    100% {
      transform: scale(1);
    }
  }
/* Gradient animation */
body {
    animation: gradient 10s linear infinite;
  }
  
  @keyframes gradient {
    0% {
      background: linear-gradient(45deg, #ff3366, #ff9933);
    }
    25% {
      background: linear-gradient(45deg, #ff9933, #33ccff);
    }
    50% {
      background: linear-gradient(45deg, #33ccff, #ff3366);
    }
    75% {
      background: linear-gradient(45deg, #ff3366, #ff9933);
    }
    100% {
      background: linear-gradient(45deg, #ff9933, #33ccff);
    }
  }
/* Animated background image */
body {
    background-image: url('research.png');
    animation: slide 20s linear infinite;
  }
  
  @keyframes slide {
    0% {
      background-position: 0% 0%;
    }
    100% {
      background-position: 100% 0%;
    }
  }
/* Particle animation (CSS) */
/* For a full particle animation, consider using a JavaScript library like particles.js */
body {
    background: #000;
    overflow: hidden;
  }
/* Gradient animation with text overlay */
body {
    background: linear-gradient(to right, #ff3366, #33ccff);
    animation: gradientText 10s linear infinite;
  }
  
  @keyframes gradientText {
    0%, 100% {
      background: linear-gradient(to right, #ff3366, #33ccff);
    }
    50% {
      background: linear-gradient(to right, #33ccff, #ff3366);
    }
  }
  
  /* Centered text */
  .container {
    text-align: center;
    color: #fff;
    text-shadow: 2px 2px 4px rgba(0, 0, 0, 0.5);
  }
                  